  getDateTime(d1: any) {
    var d = new Date(d1);
    var r =
      d.getUTCFullYear() +
      '-' +
      d.getUTCMonth() +
      '-' +
      d.getUTCDate() +
      ' ' +
      d.getUTCHours() +
      ':' +
      d.getUTCMinutes() +
      ':' +
      d.getUTCSeconds();
    return r;
  }
