  GenPassword(n: number = 6) {
    const key = 'abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789~!@#$%^&*()[]{}|:;<>?';
    let pwd: string = '';
    for (let i = 0; i < n; i++) {
      const r = Math.floor(Math.random() * Math.floor(key.length - 1));
      pwd = pwd.concat(key.charAt(r));
    }
    return pwd;
  }

  toCheckAlpha(v) {
    let message = '';
    var letterNumber = /^[0-9a-zA-Z]+$/;
    if (v.match(letterNumber)) {
      return (message = '');
    } else {
      return (message = 'Please enter a alphanumaric password');
    }
  }

  validatePassword(p: string, errors: any[]) {
    // if(errors.length>5){
    //   errors=[];
    // }
    try {
      if (p.length < 8) {
        errors.push('Your password must be at least 8 characters');
      }
      if (p.search(/[a-z]/i) < 0) {
        errors.push('Your password must contain at least one small letter.');
      }
      var format = /[A-Z]/;
      if (format.test(p)) {
      } else {
        errors.push('Your password must contain at least one capital letter.');
      }

      // if (p.search(/[A-Z]/i) < 0) {
      //   errors.push('Your password must contain at least one capital letter.');
      // }
      if (p.search(/[0-9]/) < 0) {
        errors.push('Your password must contain at least one digit.');
      }

      format = /[!@#$%^&*()_+\-=\[\]{};':'\\|,.<>\/?]+/;
      if (format.test(p)) {
      } else {
        errors.push(
          'Your password must contain at least one special character.'
        );
      }
      if (errors.length > 0) {
        // alert(errors.join('\n'));
        return false;
      }
      return true;
    } catch (e) {
      return false;
    }
  }

  /* Common Method*/
  GetCountry(code: string) {
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < this.conf.countries.length; i++) {
      if (code.toLowerCase() == this.conf.countries[i].code.toLowerCase()) {
        return this.conf.countries[i].name.toUpperCase();
      }
    }
  }

  LocalTimeString(ms) {
    var d = new Date(parseFloat(ms));
    const myMoment: moment.Moment = moment(d);
    return myMoment.format('YYYY-MM-DD hh:mm A');
  }

  TimeString(ms, s = 0) {
    var d = new Date(parseFloat(ms));
    const myMoment: moment.Moment = moment(d);
    return myMoment.format('YYYY-MM-DD') + (s == 0 ? ' 00:00:00' : ' 23:59:59');
  }

  getTimeStamp() {
    var time = new Date().getTime();
    var d = new Date(time);
    const myMoment: moment.Moment = moment(d);
    const Timestamp = {
      sCreadedOn: time,
      sCreadedOn_Str: myMoment.format('YYYY-MM-DD hh:mm A'),
      sUpdatedOn: time,
      sUpdatedOn_Str: myMoment.format('YYYY-MM-DD hh:mm A')
    };
    return Timestamp;
  }

  DateString(str: string, op: number = 0) {
    if (op == 0) {
      return str.split(' ')[0];
    } else {
      return str.split(' ')[1];
    }
  }
