import { GlobalService } from './global.service';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class HeaderService {
  public loggedUserDetail: any;
  public walletBalance: any;
  public walletId: number;

  public setLoggedUserDetail = new BehaviorSubject<any>(this.loggedUserDetail);
  castLoggedUserDetail = this.setLoggedUserDetail.asObservable();

  public setWalletBalance = new BehaviorSubject<any>(this.walletBalance);
  castWalletBalance = this.setWalletBalance.asObservable();

  constructor(
    private globalService: GlobalService,
  ) {
    this.getLoggedUserDetail();
   }


  getLoggedUserDetail() {
    this.loggedUserDetail = JSON.parse(localStorage.getItem('LoggedUser'));
    // console.log(this.loggedUserDetail);
    this.walletId = this.loggedUserDetail.sProfile.sWalletId;
    this.setLoggedUserDetail.next(this.loggedUserDetail);

    this.getUserWalletDetail();
  }
  getUserWalletDetail() {
    const params = {
      sWalletId: this.walletId
    };
    this.globalService.getWalletInfo(params).subscribe(data => {
      console.log(data);
      // this.walletId = data.uBalance;
      this.setWalletBalance.next(data.uBalance);
    });
   }
   updateLocalStorageSProfile(data: any) {
      this.loggedUserDetail.sProfile = data;
      localStorage.setItem('LoggedUser', JSON.stringify(this.loggedUserDetail));
   }
}
